#login-form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 1.3rem;

    .form-group {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }

    input {
        padding: 10px 20px;
        background-color: #eee;
        border: none;
        border-radius: 4px;
        font-size: 1.3rem;
    }

    h2 {
        margin: 0;
        padding-bottom: 10px;
        border-bottom: 1px solid #eee;
    }

    button {
        padding: 10px 20px;
        background-color: #eee;
        border: none;
        border-radius: 4px;
        font-size: 1.3rem;
        cursor: pointer;
        background-color: #69BA6A;
    }
}