.post-new {
    display: flex;
    flex-direction: column;

    .editor {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }

    .editor textarea {
        outline: none;
        width: 100%;
        flex-grow: 1;
        resize: none;
        border: none;
        padding: 20px;
        font-size: 1.3rem;
        font-family: "PorximaNova-Regular", sans-serif;
    }

    .controls {
        padding: 20px;
        display: flex;
        justify-content: flex-end;
    }
}